import {
	AfterViewInit,
	ChangeDetectionStrategy,
	Component,
	ElementRef,
	Input,
	QueryList,
	Renderer2,
	ViewChild,
	ViewChildren,
	ViewEncapsulation
} from '@angular/core';

@Component({
	selector: 'gui-tab',
	templateUrl: `tab.component.html`,
	styleUrls: [
		'./tab.ngx.scss',
		'./themes/tab.material.ngx.scss',
		'./themes/tab.dark.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-tab]': 'true'
	}
})
export class FabricTabComponent implements AfterViewInit {

	@ViewChild('tab', { static: false })
	tabRef: ElementRef;

	@ViewChild('tabitem', { static: false })
	tabItemRef: ElementRef;

	@ViewChildren('tabMenuList')
	tabMenuList: QueryList<ElementRef>;

	@Input()
	menu: Array<string>;

	@Input()
	active: string;

	@Input()
	scrollActive: boolean = false;

	private listPosition: number = 0;
	private menuListWidth: number = 0;
	private readonly scrollAmount = 60;

	private readonly ACTIVE_TAB_CLASS_NAME = 'gui-active';

	constructor(private renderer: Renderer2) {
	}

	ngAfterViewInit() {
		this.toggleTab(this.active);
		this.calculateMenuWidth();
	}

	toggleTab(tab: string): void {
		this.removeActive();
		this.setActive(tab);
	}

	scrollTabList(scrollRightClicked: boolean): void {

		let listWidth = this.tabRef.nativeElement.querySelector('.gui-tab-menu-list').offsetWidth,
			menuOverflow = this.menuListWidth - listWidth;

		if (scrollRightClicked && menuOverflow > this.listPosition) {
			this.listPosition += this.scrollAmount;
		} else if (!scrollRightClicked && this.listPosition > 0) {
			this.listPosition -= this.scrollAmount;
		}

		this.tabRef.nativeElement.querySelector('.' + 'gui-tab-menu-list').scrollLeft = this.listPosition;
	}

	private setActive(tab: string): void {

		const navMenuTabEl = this.tabRef.nativeElement.querySelector('[data-tab="' + tab + '"]'),
			navTabItemEl = this.tabItemRef.nativeElement.querySelector('[data-tab="' + tab + '"]');

		this.addClass(navMenuTabEl, this.ACTIVE_TAB_CLASS_NAME);
		this.addClass(navTabItemEl, this.ACTIVE_TAB_CLASS_NAME);
	}

	private removeActive(): void {

		const navMenuTabEl = this.tabRef.nativeElement.querySelector('.' + this.ACTIVE_TAB_CLASS_NAME),
			navTabItemEl = this.tabItemRef.nativeElement.querySelector('.' + this.ACTIVE_TAB_CLASS_NAME);

		this.removeClass(navMenuTabEl, this.ACTIVE_TAB_CLASS_NAME);
		this.removeClass(navTabItemEl, this.ACTIVE_TAB_CLASS_NAME);
	}

	private addClass(element: any, name: string) {
		if (element) {
			this.renderer.addClass(element, name);
		}
	}

	private removeClass(element: any, name: string) {
		if (element) {
			this.renderer.removeClass(element, name);
		}
	}

	private calculateMenuWidth(): void {

		if (this.scrollActive) {
			this.tabMenuList
				.forEach(
					(listItem: ElementRef) => this.menuListWidth += listItem.nativeElement.offsetWidth
				);
		}
	}

}
